.backdrop {
  width: 100%;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop .modal-sec {
  width: 335px;
  height: 100%;
}

.backdrop .form-first__close {
  height: 35px;
  width: 40px;
  cursor: pointer;
  background-color: #fff;
  border-color: #fff currentColor currentColor #fff;
  border-bottom-style: none;
  border-bottom-width: 0;
  border-right-style: none;
  border-right-width: 0;
  margin-top: 8px;
  margin-left: auto;
  margin-right: 8px;
  padding-top: 6px;
}

.backdrop .modal {
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.backdrop .modal .form-first__title {
  color: #ae1332;
