.squaredThree {
  width: 20px;
  margin: 15px auto 20px;
  position: relative;
  left: 20px;
}

.squaredThree label {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: #f8766b;
  border-color: #eee;
  border-radius: 4px;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: inset 1px 1px 1px #fff, 1px 1px 1px #fff;
}

.squaredThree label:after {
  content: "";
  width: 9px;
  height: 5px;
  opacity: 0;
  background: none;
  border-top: none;
  border-bottom: 3px solid #fcfff4;
  border-left: 3px solid #fcfff4;
  border-right: none;
  position: absolute;
  top: 6px;
  left: 6px;
  transform: rotate(-45deg);
}

.squaredThree input[type="checkbox"] {
  visibility: hidden;
}

.squaredThree input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

